body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  position: relative;
}

nav {
  height: 100%;
  margin: 0 50px 0 0;
}

img {
  margin: 10px 50px;
}

ul {
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
}

li {
  margin: 0 0 0 20px;
  list-style: none;
  display: inline-block;
}

li:first-child {
  margin: 0;
}

a {
  color: #000;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

a:hover {
  color: #00acdc;
  transition: all .3s;
}

a.is-active {
  color: #00acdc;
}

a.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
